import React,{useState,useEffect} from 'react';
import {Text, View,StyleSheet, Button, TextInput} from 'react-native';


import result from './result';




const HomeScreen =({navigation})=>{
    const[no,setNo]=useState('');
   const[PhoneApi,results,OtpArray]=result();
   console.log('djd',{results});
   console.log('djfvgfd',{OtpArray});
  

    return(
    <View style={styles.contain}>
    
        <Text style={styles.press}>Enter Your Mobile Number {OtpArray}</Text>
        <TextInput style={styles.textin} onChangeText={setNo} placeholder="Enter 10 Digit Phone Number" />
        <View >
        <Button 
        title='Submit'
        onPress={()=> PhoneApi(no)}
        />
      <Text style={{margin:5}}></Text>
       
       <Button 
        title='Responsive Image'
        onPress={()=>navigation.navigate('Image')}
        />
        <Text style={{margin:5}}></Text>
         <Button 
        title='Feed'
        onPress={()=>navigation.navigate('Feed')}
        />
        </View>
    </View>
    );

};

const styles=StyleSheet.create({
    textin:{
        backgroundColor:'lightgrey',
        borderRadius:5,
        marginBottom:10
    },
    contain:{
        

            flex: 1,
    
            justifyContent: 'center',
    
            alignItems: 'center',
    
        
    
        margin:10
    },
    press:{
        marginTop:10,
        alignContent:'center'

    },contain2:{
        
        flex:1,
        
    }

})

export default HomeScreen;